/* Your own custom utilities */
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@600;900&display=swap');

html,
body {
  width: 100%;
  height: 100%;
  font-family: 'Work Sans';
  font-weight: 600;
  overflow: hidden;
}

input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 17px;
  height: 17px;
  background: black;
  border-radius: 50%;
}

*:not(input):not(textarea) {
  -ms-user-select: none;
      user-select: none; /* disable selection/Copy of UIWebView */
  -webkit-user-select: none; /* disable selection/Copy of UIWebView */
  -webkit-touch-callout: none; /* disable the IOS popup when long-press on a link */
}

@supports (-webkit-touch-callout: none) {
  .full-visible-h-safari {
    height: calc(100% - 80px); /* -webkit-fill-available;*/
  }
}

.focus-within\:outline-none[focus-within] {
	outline: 2px solid transparent;
	outline-offset: 2px;
}

.focus-within\:outline-none:focus-within {
	outline: 2px solid transparent;
	outline-offset: 2px;
}

.hover\:border-black:hover {
	--tw-border-opacity: 1;
	border-color: rgba(0, 0, 0, var(--tw-border-opacity));
}

.hover\:bg-black:hover {
	--tw-bg-opacity: 1;
	background-color: rgba(0, 0, 0, var(--tw-bg-opacity));
}

.hover\:bg-primary:hover {
	--tw-bg-opacity: 1;
	background-color: rgba(189, 255, 1, var(--tw-bg-opacity));
}

.hover\:text-white:hover {
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}

.focus\:outline-none:focus {
	outline: 2px solid transparent;
	outline-offset: 2px;
}

@media (min-width: 640px) {

	.sm\:block {
		display: block;
	}

	.sm\:hidden {
		display: none;
	}

	.sm\:h-52 {
		height: 13rem;
	}

	.sm\:h-auto {
		height: auto;
	}

	.sm\:flex-row {
		flex-direction: row;
	}

	.sm\:items-start {
		align-items: flex-start;
	}

	.sm\:items-center {
		align-items: center;
	}

	.sm\:justify-center {
		justify-content: center;
	}

	.sm\:justify-between {
		justify-content: space-between;
	}

	.sm\:space-y-0 > :not([hidden]) ~ :not([hidden]) {
		--tw-space-y-reverse: 0;
		margin-top: calc(0px * calc(1 - var(--tw-space-y-reverse)));
		margin-bottom: calc(0px * var(--tw-space-y-reverse));
	}

	.sm\:space-x-6 > :not([hidden]) ~ :not([hidden]) {
		--tw-space-x-reverse: 0;
		margin-right: calc(1.5rem * var(--tw-space-x-reverse));
		margin-left: calc(1.5rem * calc(1 - var(--tw-space-x-reverse)));
	}

	.sm\:p-12 {
		padding: 3rem;
	}

	.sm\:p-0 {
		padding: 0px;
	}

	.sm\:p-14 {
		padding: 3.5rem;
	}

	.sm\:px-0 {
		padding-left: 0px;
		padding-right: 0px;
	}

	.sm\:text-left {
		text-align: left;
	}

	.sm\:text-3xl {
		font-size: 1.875rem;
		line-height: 2.25rem;
	}

	.sm\:text-sm {
		font-size: 0.875rem;
		line-height: 1.25rem;
	}
}

@media (min-width: 768px) {

	.md\:inline {
		display: inline;
	}

	.md\:w-auto {
		width: auto;
	}
}
